.jcc-breadcrumbs-nav {
  &.has-back-button {
    @include u-display("flex");
    @include u-align-items("align-center");

    .usa-button--back {
      @include u-margin-right(3);
    }
  }
}

.jcc-breadcrumbs {
  @include add-list-reset;
  @include u-text(base-dark);
}

.jcc-breadcrumb__item {
  @include responsive-lead();
  @include u-padding-right(1);
  @include u-display(inline);
  @if $trial {
    font-size: 1.06rem !important; // !important not supported by mixin
    @include jcc-text-color(primary-blue-dark);
  }

  a {
    @include u-text(info-darker);
    text-decoration: none;
    @if $trial {
      @include jcc-text-color(primary-blue-vivid-dark);
    }

    &:after {
      @include u-display(inline-block);
      content: "/";
      margin-left: 0.6275rem;
    }
  }
}
